import { head } from "./fable_modules/fable-library.3.4.0/Array.js";
import { toConsoleError, toConsole, printf, toText, split } from "./fable_modules/fable-library.3.4.0/String.js";
import { UserInfo_Parse_Z721C83C5, Options } from "./OidcClient.fs.js";
import { UserManager } from "oidc-client";
import { now, compare, fromTicks } from "./fable_modules/fable-library.3.4.0/Date.js";
import { fromInteger } from "./fable_modules/fable-library.3.4.0/Long.js";

const options = (() => {
    const url = head(split(window.location.href, ["?", "#"]));
    return new Options("https://fablecriipto-test.criipto.id", "urn:my:application:identifier:1744", url, "code", "query", url, "urn:grn:authn:dk:mitid:low");
})();

const userManager = new UserManager(options);

export const storageKey = toText(printf("oidc.user:%s:%s"))(options.authority)(options.client_id);

export const storage = sessionStorage;

export function tryGetUserProfile() {
    const userInfo = storage.getItem(storageKey);
    if (userInfo == null) {
        return void 0;
    }
    else {
        return UserInfo_Parse_Z721C83C5(userInfo);
    }
}

export function hasRequestedAuthentication() {
    return window.location.href.indexOf("code=") >= 0;
}

export function isAuthenticated() {
    const matchValue = tryGetUserProfile();
    if (matchValue != null) {
        const expiresAt = fromTicks(fromInteger(matchValue.expires_at, false, 2));
        toConsole(printf("Expires at %A"))(expiresAt);
        return compare(expiresAt, now()) > 0;
    }
    else {
        return true;
    }
}

export function logOut() {
    try {
        storage.removeItem(storageKey);
        userManager.signoutRedirect();
    }
    catch (e) {
        const arg10 = e.message;
        toConsoleError(printf("Eror when logging out: %s"))(arg10);
    }
}

export function logIn() {
    return userManager.signinRedirect();
}

export function registerLogin(setUser) {
    const matchValue = tryGetUserProfile();
    if (matchValue == null) {
        void userManager.processSigninResponse().then((userInfo) => {
            const json = JSON.stringify(userInfo);
            storage.setItem(storageKey, toText(printf("%A"))(json));
            setUser(userInfo);
        }).catch((err) => {
            console.error("Error:", err);
            setUser(void 0);
        });
    }
    else {
        setUser(matchValue);
    }
}

